@import url('https://fonts.googleapis.com/css?family=Questrial');

body {
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#7dec65+0,2dc198+50,2dc198+50,4e58c4+100 */
    background: rgb(125,236,101); /* Old browsers */ /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgba(125,236,101,1) 0%,rgba(45,193,152,1) 50%,rgba(45,193,152,1) 50%,rgba(78,88,196,1) 100%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(125,236,101,1)),color-stop(50%, rgba(45,193,152,1)),color-stop(50%, rgba(45,193,152,1)),to(rgba(78,88,196,1)));
    background: -o-linear-gradient(top, rgba(125,236,101,1) 0%,rgba(45,193,152,1) 50%,rgba(45,193,152,1) 50%,rgba(78,88,196,1) 100%);
    background: linear-gradient(to bottom, rgba(125,236,101,1) 0%,rgba(45,193,152,1) 50%,rgba(45,193,152,1) 50%,rgba(78,88,196,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#7dec65', endColorstr='#4e58c4',GradientType=0 ); /* IE6-9 */
    font-family: 'Questrial', sans-serif;
}

main,
.editor-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

.title {
    text-shadow: 2px 2px 5px grey;
    font-size: 32px;
}

#editor,
#preview {
    margin: 10px 2vw 2vw;
    width: 80vw;
    padding: 20px;
    background: white;
}

#editor {
    height: 40vh;
}


